<script>
import { Dialog, ImagePreview, Toast } from "vant";
import { getFromNom } from "@js/moment";
export default {
  components: {
    [Dialog.Component.name]: Dialog.Component
  },
  data() {
    return {
      itemst: [],
      share: this.$route.query.share, // 1: 是从分享页 打开
      iconLoveNormal: require("../../assets/images/icon_love_normal.png"),
      iconLoveSelection: require("../../assets/images/icon_love_selection.png"),
      iconSteponSelected: require("../../assets/images/icon_stepon_selected.png"),
      iconSteponNormal: require("../../assets/images/icon_stepon_normal.png"),
      iconUserSelecte: require("../../assets/images/icon_user_selecte.png"),
      iconUserNormal: require("../../assets/images/icon_user_normal.png"),
      text: "均分打赏",
      inputShow: true,
      expression: [],
      expressionShow: false,
      is: 0,
      data: {},
      All: {},
      list: [],
      allpage: -1,
      loading: false,
      finished: false,
      item: {
        token: "",
        tid: "",
        page_index: 0,
        page_size: 10
      },
      ImagePreviewDialog: "", //查看图片
      items: {
        //打赏参数
        token: this.$store.state.userData,
        tid: "",
        reward_type: "",
        pid: "",
        pids: ""
      },
      parameter: {
        //点赞参数
        token: this.$store.state.userData,
        type: "",
        pid: "",
        tid: ""
      },
      postParameter: {
        token: this.$store.state.userData,
        tid: "",
        pid: "",
        aid_list: "",
        attachment: 0,
        message: ""
      },
      isShow: false, //自己类型的 打赏显示隐藏
      id: [] //打赏类型ID
    };
  },
  mounted() {
    this.init();
    this.getSmiley();
    document.title = " ";
  },
  methods: {
    getSmiley() {
      this.api.getSmiley().then(res => {
        if (res.err_code == 0) {
          this.expression = res.data.common_smiley[0].list;
        }
      });
    },
    init() {
      (this.item.token = this.$store.state.userData),
        (this.item.tid = this.$route.query.id),
        //回复详情
        this.api.answersDetails(this.item).then(res => {
          if (res.err_code == 0) {
            this.data = res.data.thread_data;
            this.All = res.data;
            this.detailsItem();
          }
        });
    },
    //回复详情列表
    detailsItem() {
      this.api.answersDetailsList(this.item).then(res => {
        if (res.err_code == 0) {
          this.allpage = res.data.total_num;
          console.log(res.data.post_list != []);
          if (res.data.post_list.length) {
            for (let i = 0; i < res.data.post_list.length; i++) {
              if (res.data.post_list[i].isShowSelect) {
                this.$set(res.data.post_list[i], "isShowSelect", false);
              } else {
                this.$set(res.data.post_list[i], "isShowSelect", true);
              }
            }
          }else {
            this.finished = true;
          }
          if (res.data.selected.length) {
            for (let i = 0; i < res.data.selected.length; i++) {
              if (res.data.selected[i].isShowSelect) {
                this.$set(res.data.selected[i], "isShowSelect", false);
              } else {
                this.$set(res.data.selected[i], "isShowSelect", true);
              }
            }
          }
          if(this.item.page_index == 0){
            this.list = res.data;
          }else {
            this.list.selected = [...this.list.selected,...res.data.selected]
            this.list.post_list = [...this.list.post_list,...res.data.post_list]
          }
          
          this.itemst = [...this.itemst, ...res.data.post_list];
        }
        this.loading = false;
      });
    },
    // 跳转我的页面
    handleToMyPage(item) {
      if (!this.$store.state.userData) {
        //  window.location.href= "teldapp://login"
        let frame = document.createElement("frame");
        frame.src = "teldapp://login";
        document.documentElement.childNodes[0].appendChild(frame);
        return null;
      }
      if (item.is_self == 1) {
        this.$router.push({ path: "/my/index/square" });
      } else {
        this.$router.push({
          path: "/my/index/square",
          query: { uid: item.authorid }
        });
      }
      //this.$router.push({path:'/my/index/square',query:{uid:''}})
    },
    onLoad() {
      if (this.itemst.length) {
        this.item.page_index++;
        this.detailsItem();
      }
      if (this.item.page_index == this.allpage) {
        this.finished = true;
        this.loading = false
      }
    },
    //点击表情取消聚焦
    getSmileyClick() {
      this.expressionShow = true;
      let id = document.getElementById("id");
      console.log(console.log(id));
      id.style.bottom = "5.33333rem";
    },
    expressionReplyClick(message) {
      let data = {
        token: this.$store.state.userData,
        tid: this.data.tid,
        pid: "",
        message: message,
        aid_list: 0,
        attachment: ""
      };
      this.api.addQuestionReply(data).then(res => {
        console.log(res);
        if (res.err_code == 0) {
          this.expressionShow = false;
          this.inputShow = true;
          let id = document.getElementById("id");
          console.log(console.log(id));
          id.style.bottom = "0";
        }
      });
    },
    //均分打赏
    rewardClick() {
      this.isShow = !this.isShow;
      if (this.isShow) {
        this.text = "取消";
        //最新
        for (let i = 0; i < this.list.post_list.length; i++) {
          this.id.push(this.list.post_list[i].pid);
        }
        // 精选
        for (let i = 0; i < this.list.selected.length; i++) {
          this.id.push(this.list.selected[i].pid);
        }
      } else {
        this.text = "均分打赏";
        this.id = [];
      }
    },
    // 输入框获取焦点
    animateWidth() {
      if(this.data.has_reply){
        this.answersDetailsClick(this.data.has_reply)
      }
      //this.answersDetailsClick();
      // setTimeout(function(){
      window.scrollTo(0, document.body.scrollHeight);
      // this.inputShow = false;
      // },300);
    },
    blurClick() {
      // this.inputShow = true;
    },
    // 回复
    addPost() {
      this.inputShow = true;
      if (!this.$store.state.userData) {
        // window.location.href = "teldapp://login";
        let frame = document.createElement("frame");
        frame.src = "teldapp://login";
        document.documentElement.childNodes[0].appendChild(frame);

        return null;
      }
      if (!this.postParameter.message) {
        return Toast("输入不能为空");
      }
      Toast.loading({
        message: "加载中...",
        forbidClick: true
      });
      this.postParameter.message = this.postParameter.message.replace(/ /g,'')
      this.postParameter.tid = this.data.tid;
      this.api.addQuestionReply(this.postParameter).then(res => {
        Toast.clear();
        if (res.err_code == 0 && !res.data.status) {
          this.$toast("回答成功！");
          this.item.item = 0;
          this.postParameter.message = "";
          this.init();
        } else if (res.err_code == 0 &&res.data.status == -1) {
          Toast(res.data.message);
          this.postParameter.message = "";
        }else {
          Toast(res.data.message);
        }
      });
    },
    //全部打赏 均分打赏
    selectAdoptClick() {
      if (this.id.length == 0) {
        this.$toast("请选择打赏人");
        return false;
      } else {
        Dialog.confirm({
          title: "标题",
          message: `是否打赏选择的${this.id.length}个人`
        })
          .then(() => {
            this.items.tid = this.item.tid;
            this.items.reward_type = 4;
            for (let i = 0; i < this.id.length; i++) {
              this.items.pids = this.id + ",";
            }
            this.api.reward(this.items).then(res => {
              if (res.err_code == 0) {
                this.$toast.success("打赏成功");
                this.isShow = false;
                this.init();
              } else {
                this.$toast.fail(res.err_msg);
              }
            });
          })
          .catch(() => {});
      }
    },
    //单独采纳打赏
    aloneRewardClick(id) {
      this.items.reward_type = 1;
      this.items.pid = id;
      this.api.reward(this.items).then(res => {
        if (res.err_code == 0) {
          this.$toast.success("打赏成功");
          this.isShow = false;
          this.items.pid = "";
        } else {
          this.$toast.fail(res.data.msg);
        }
      });
    },
    // 打赏选择
    imgSelectClick(item, id) {
      //最新
      for (let i = 0; i < this.list.post_list.length; i++) {
        //清除选中的
        if (this.list.post_list[i].pid == id) {
          if (item) {
            this.list.post_list[i].isShowSelect = false;
            for (let i = 0; i < this.id.length; i++) {
              if (this.id[i] == id) {
                this.id.splice(i, 1);
              }
            }
          } else {
            //加入选中的
            this.list.post_list[i].isShowSelect = true;
            this.id.push(this.list.post_list[i].pid);
          }
        }
      }
      // 精选
      for (let i = 0; i < this.list.selected.length; i++) {
        this.id.push(this.list.selected[i].fid);
      }
    },
    //取消全部选中的打赏
    cancelClick() {
      for (let i = 0; i < this.list.post_list.length; i++) {
        if (this.list.post_list[i].isShowSelect) {
          this.list.post_list[i].isShowSelect = false;
        }
      }
      for (let i = 0; i < this.list.selected.length; i++) {
        if (this.list.selected[i].isShowSelect) {
          this.list.selected[i].isShowSelect = false;
        }
      }
      this.id = [];
    },
    //返回上一页
    onClickLeft() {
      this.$router.go(-1);
    },
    // 分享
    onClickRight() {
      let img = this.data.image_list.length
        ? this.data.image_list[0]
        : this.data.avatar;
      let url = `${this.$store.state.indexUrl +
          "answers/answersDetails?share=1" +
          "&" +
          this.$store.state.appParams}`,
        json = {
          url: url,
          channel: "wxp,wxf,sina,qqp,qqz",
          title: `【E区】我在E区发布了一条有偿问答，快来帮帮我吧`,
          desc: this.data.message,
          image: img
        };
      console.log(url);
      window.location.href = `teldapp://share?params=` + JSON.stringify(json);
    },
    //评论点赞
    normalClick(id, pid,item) {
      this.parameter.tid = id;
      this.parameter.pid = pid;
      this.parameter.type = 1;
      this.api.addZanPost(this.parameter).then(res => {
        if (res.err_code == 0) {
          if(item.is_zan == 1){
            item.is_zan = 0;
            item.zan = item.zan - 1;
          }else{
            if(item.is_zan == 2){
              item.cai = item.cai -1;
            }
            item.is_zan = 1;
            item.zan = item.zan + 1;
          }
          // for (let i = 0; i < this.list.post_list.length; i++) {
          //   if (this.list.post_list[i].tid == id) {
          //     this.list.post_list[i].is_zan = 1;
          //   }
          // }
          // for (let i = 0; i < this.list.selected.length; i++) {
          //   if (this.list.selected[i].tid == id) {
          //     this.list.selected[i].is_zan = 1;
          //   }
          // }
          this.detailsItem();
          this.$toast(res.data.msg);
        }
      });
    },
    //评论点踩
    steponSelectedClick(id, pid,item) {
      this.parameter.tid = id;
      this.parameter.pid = pid;
      this.parameter.type = 2;
     
      this.api.addZanPost(this.parameter).then(res => {
        if (res.err_code == 0) {
          if(item.is_zan == 2){
            item.is_zan = 0;
            item.cai = item.cai - 1;
          }else{
            if(item.is_zan == 1){
              item.zan = item.zan -1;
            }
            item.is_zan = 2;
            item.cai = item.cai + 1;
          }
          
          // for (let i = 0; i < this.list.post_list.length; i++) {
          
          //   if (this.list.post_list[i].tid == id) {
          //     this.list.post_list[i].is_zan = 2;
          //   }
          // }
          // for (let i = 0; i < this.list.selected.length; i++) {
          //   if (this.list.selected[i].tid == id) {
          //     this.list.selected[i].is_zan = 2;
          //   }
          // }
          this.detailsItem();
          this.$toast(res.data.msg);
        }
      });
    },
    //列表 点赞
    postZanClick(id) {
      this.parameter.tid = id;
      this.parameter.pid = "";
      this.parameter.type = 1;
      this.api.addZan(this.parameter).then(res => {
        if (res.err_code == 0) {
          this.data.is_zan = 1;
          this.$toast(res.data.msg);
          this.init();
        }
      });
    },
    //列表 点踩
    postCaiClick(id) {
      this.parameter.tid = id;
      this.parameter.pid = "";
      this.parameter.type = 2;
      this.api.addZan(this.parameter).then(res => {
        if (res.err_code == 0) {
          this.data.is_zan = 2;
          this.$toast(res.data.msg);
          this.init();
        }
      });
    },
    //回答列表 详情
    answersDetailsClick(pid) {
      // console.log();
      let type = this.data.has_reply == pid ? 'me':'other'
      if (this.share != 1) {
      }
      // this.$router.push({
      //   path: "/answers/answersDetails/askingDetails",
      //   query: { pid: pid, tid: this.data.tid,type:type }
      // });
      
      let url = `${this.$store.state.indexUrl +
          "answers/answersDetails/askingDetails?pid=" +
          pid +
          "&tid=" +
          this.data.tid +
          "&type" + type +
          "&" +
          this.$store.state.appParams}`,
        json = { url: url, share: "0" ,keyboard:'1'};
      window.location.href =
        `teldapp://newWebview?params=` + JSON.stringify(json);
  
    },
    //查看图片
    answersImageClick(image, index) {
      this.ImagePreviewDialog = ImagePreview({
        images: image,
        loop: false
        // startPosition: index
      });
    }
  },
  filters: {
    timeformat(v) {
      if (v) {
        return getFromNom(v * 1000);
      } else {
        return "";
      }
    }
  }
};
</script>
